<template>
    <input type="text" v-model.number="number1">
    <input type="text" v-model.number="number2">
    
    <div class="cal">
    <button @click="addcal">덧샘계산</button>
	<button @click="subcal">뺄샘계산</button>
	<button @click="multical">곱샘계산</button>
	<button @click="divcal">나눗샘계산</button>
    </div>
    <p>결과는 {{ result }} 입니다</p>
</template>

<script>
export default{
    data(){
        return{
           number1: 0,
           number2: 0,
           result: 0
        }
    },
    methods: {
        addcal:function() {
			this.result = this.number1 +  this.number2;
		},
		subcal:function() {
			this.result = this.number1 -  this.number2;
		},
		multical:function() {
			this.result = this.number1 *  this.number2;
		},
		divcal:function() {
			this.result = this.number1 /  this.number2;
		},
    }
}
</script>

